.form {
  background-color: #fff;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.form label {
  width: 100%;
  margin-bottom: 10px;
  display: block;
}

.form input[type="number"] {
  width: 100%;
  max-width: 200px;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 8px;
}

.form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .3s ease-in-out;
}

.form button[type="submit"]:hover {
  background-color: #0069d9;
}

p a {
  color: #007bff;
  font-size: 14px;
  text-decoration: none;
}

p a:hover {
  text-decoration: underline;
}

